.carousel-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

.carousel-wrapper {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: column;
}

.carousel-content-wrapper {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.carousel-content {
  display: flex;
  transition: all 250ms linear;
  -ms-overflow-style: none; /* hide scrollbar in IE and Edge */
  scrollbar-width: none; /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel-content::-webkit-scrollbar,
.carousel-content::-webkit-scrollbar {
  display: none;
}

.carousel-content > * {
  width: 100%;
  flex-shrink: 0;
  flex-grow: 1;
}

.carousel-buttons {
  display: flex;
  justify-content: center;
  cursor: pointer;
  color: gray;
}
